$border-color: #406465;

.input {
  width: 100%;
  font-size: 16px;
  border-radius: 3px;
  height: 40px;
  padding-left: 10px;
  padding-right: 10px;
  border: 1px solid $border-color;
  &:disabled {
    opacity: 0.5;
  }
  &.small {
    height: 30px;
  }
  &.large {
    height: 60px;
    font-size: 20px;
  }
}
