.App {
  h1,
  h2 {
    text-align: center;
    width: 100%;
  }
  section {
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-wrap: wrap;
    > * {
      margin-top: 5px;
      margin-bottom: 5px;
    }

    &.icons {
      display: flex;
      flex-direction: row;
      justify-content: center;
      > div {
        width: 100px;
        height: 80px;
        display: flex;
        flex-direction: column;
        // border:1px solid #ccc;
        justify-content: space-between;
        align-items: center;
        padding: 10px;
      }
    }
  }
}
